import axios from 'axios';

// cart data
let cart = [];
let buttonsDOM = [];
const cartTotal = document.querySelector('.cart-total');
const cartItems = document.querySelectorAll('.cart-items');
const cartContent = document.querySelector('.cart-content');
const cartDOM = document.querySelector('.cart');
const cartOverlay = document.querySelector('.cart-overlay');
const clearCartBtn = document.querySelector('.clear-cart');



// Getting products
class Products {
  async fetchProducts() {
    try {
      // let response = await fetch("products.json")
      // console.log(response)
      // let data = await response.text();
      let response = await axios.get('products.json');
      let products = response.data.items;
     
      products = products.map(item => {
        const { title, price } = item.fields;
        const { id } = item.sys;
        const image = item.fields.image.fields.file.url;
        return { title, price, id, image };
      })
      
      return products;
    } catch (error) {
      console.log(error)
    }
  }
}
// display products
class UI {
displayProducts(products){
  const productDOM = document.querySelector('.products-center');
 

  let result = '';
  products.forEach(product => {
    result += `
    <article class="product">
          <div class="img-container">
            <img src=${product.image} alt="class 1" class="product-img">
            <button class="bag-btn" data-id=${product.id}>
              <i class="fas fa-shopping-cart"></i>
              add to cart
            </button>
          </div>
          <h3>${product.title}</h3>
          <h4>£${product.price}</h4>
    </article>
    `
  });
  productDOM.innerHTML = result;
}

getBagButtons() {
  const buttons = [ ...document.querySelectorAll('.bag-btn') ];  //spread operator to make NodeList into array.
  buttonsDOM = buttons;
  buttons.forEach(button => {
    let id = button.dataset.id;
    let isInCart = cart.find(item => item.id === id);
    if(isInCart) {
      button.innerText = "In Cart";
      button.disabled = true
    } 
      button.addEventListener('click', (e) => {
       e.target.innerText = "In Cart";
       e.target.disabled = true;

      //  get product from local storage
      let cartItem = {...Storage.getProduct(id), amount: 1} //amount for total no of item in cart usage

      // add product to cart 
      cart = [ ...cart, cartItem ]   //Add cartItem along with existing item in cart
      Storage.saveCart(cart);

      // set cart values
      this.setCartValues(cart)

      // Display cart items
      this.addCartItem(cartItem)

      // Show cart
      this.showCart()
      })
    
  })
}

setCartValues(cart) {
  let tempTotal = 0;
  let itemsTotal = 0;
  cart.map(item => {
    tempTotal += item.price * item.amount;
    itemsTotal += item.amount;
  })
  cartTotal.innerText = parseFloat(tempTotal.toFixed(2));
  cartItems.forEach(item => {
    item.innerText = itemsTotal;
  })
}

addCartItem(item) {
  const div = document.createElement('div');
  div.classList.add('cart-item');
  div.innerHTML = `
  <img src=${item.image} alt="product">
            <div>
              <h4>${item.title}</h4>
              <h5>£${item.price}</h5>
              <span class="remove-item" data-id=${item.id}>remove</span>
            </div>
            <div>
              <i class="fas fa-chevron-up" data-id=${item.id}></i>
              <p class="item-amount">${item.amount}</p>
              <i class="fas fa-chevron-down" data-id=${item.id}></i>
            </div>
  `;
  cartContent.appendChild(div)
 
}

showCart() {
  cartOverlay.classList.add('transparentBcg');
  // cartDOM.classList.add('show-cart');
  cartDOM.style.transform = 'translateX(0)';
}

hideCart() {
  cartOverlay.classList.remove('transparentBcg');
  // cartDOM.classList.add('show-cart');
  cartDOM.style.transform = 'translateX(100%)';
}

setupAPP() {
  cart = Storage.getCart();
  this.setCartValues(cart);
  this.populate(cart)
}

populate(cart) {
  cart.forEach(item => this.addCartItem(item))
}

cartLogic() {
  // Clear Cart logic
  clearCartBtn.addEventListener('click', () => this.clearCart());

  // cart Functionality 
  cartContent.addEventListener('click', e => {
    if(e.target.classList.contains('remove-item')) {
      let removeItem = e.target;
      let id = removeItem.dataset.id;
      cartContent.removeChild(removeItem.closest('.cart-item'));
      this.removeItem(id)
    } else if(e.target.classList.contains('fa-chevron-up')) {
      let addAmount = e.target;
      let id = addAmount.dataset.id;
      let tempItem = cart.find(item => item.id === id);
      tempItem.amount += 1;
      Storage.saveCart(cart);
      this.setCartValues(cart);
      addAmount.nextElementSibling.innerText = tempItem.amount;
    } else if(e.target.classList.contains('fa-chevron-down')) {
      let reduceAmount = e.target;
      let id = reduceAmount.dataset.id;
      let tempItem = cart.find(item => item.id === id);
      tempItem.amount -= 1;
      if(tempItem.amount > 0) {
        Storage.saveCart(cart);
        this.setCartValues(cart);
        reduceAmount.previousElementSibling.innerText = tempItem.amount;
      } else {
        cartContent.removeChild(reduceAmount.closest('.cart-item'))
        this.removeItem(id);
      }
    }
  })
}

clearCart() {
  let cartItems = cart.map(item => item.id)
  cartItems.forEach(id => this.removeItem(id))
  // Clearing the cart in DOM
  while(cartContent.children.length > 0) {
    cartContent.removeChild(cartContent.children[0])
  }
  this.hideCart()
}

removeItem(id) {
  cart = cart.filter(item => item.id !== id)
  this.setCartValues(cart);
  Storage.saveCart(cart);
  let button = this.getSingleButton(id);
  button.disabled = false;
  button.innerHTML = `<i class="fas fa-shopping-cart"></i>add to cart`;
}

getSingleButton(id) {
  return buttonsDOM.find(button => button.dataset.id === id)
}

}

// local storage 
class Storage {
  static saveProducts(products) {
    localStorage.setItem("products", JSON.stringify(products))
  }

  static getProduct(id) {
    let products = JSON.parse(localStorage.getItem('products'));
    return products.find(product => product.id === id)
  }

  static saveCart(cart) {
    localStorage.setItem("cart", JSON.stringify(cart))
  }

  static getCart() {
    return localStorage.getItem('cart') ? JSON.parse(localStorage.getItem('cart')) : []
  }
}



const ui = new UI();
const products = new Products();

export { products, ui, Storage };
